import React, {Component} from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import {pickDevice, preSelectOffer, selectOfferMetaAction} from "../../../../modules/configurator/actions/offers";
import {OraLoader} from "eshop/components/OraCommonComponents";
import {
    getContractRole,
    getContractRoleInProgress,
    getOffersDataInContext,
    getSelectedBaseRatePlanId,
    getSelectedOfferId,
    getSelectedVases
} from "eshop/modules/configurator/selectors/offers";
import {getOffersLoading} from "eshop/modules/configurator/selectors/metaData";
import {
    getClientContext,
    getMarketContext,
    getSelectedLoyaltyLengthValue,
    getSelectedOfferType,
    getSelectedProcessTypeValue
} from "eshop/modules/configurator/selectors/filters";


import OnlineUtils from "eshop/utils/OnlineUtils";

import {getSelectedSoftBundleGroup, getSelectedVariant} from "eshop/modules/simfree/selectors";
import PropositionForDeviceMobileView from "eshop/modules/simfree/components/PropositionForDeviceMobileView"
import PropositionForDeviceTabletDesktopView
    from "eshop/modules/simfree/components/PropositionForDeviceTabletDesktopView"
import OraSwitcher from "eshop/modules/core/components/ui/OraSwitcher";
import OfferType from "../../constants/OfferTypeEnum";
import {getAddTerminalToOfferBundleNo, getEntryUnderChange} from "eshop/modules/cart/selectors";
import MarketSegment from "../../../core/enum/MarketSegment";
import OraHtmlText from "../../../core/components/ui/OraHtmlText";

class ProductDetailsPropositionsComponent extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        if ([OfferType.VOICE, OfferType.DATA, OfferType.VOICE_LDF, OfferType.DATA_LDF].indexOf(this.props.selectedOfferType) === -1) {
            return null;
        }
        const props = Object.assign({}, this.props);
        let propositions;
        console.log("ProductDetailsPropositionsComponent propositionsInContext", this.props.propositionsInContext);
        if (this.props.propositionsInContext) {
            propositions = this.props.propositionsInContext.offers;
        }
        console.log("ProductDetailsPropositionsComponent", {propositions});
        props.propositions = propositions;
        return (
            <div
                className="l-col l-col-12  g-gray1-bg u-large-no-bg u-spacing-l-xl u-medium-padding-left-l u-medium-padding-right-l u-no-padding-top u-no-margin-top">
                <div
                    className="g-gray1-bg u-medium-no-bg g-small-no-bg u-padding-all-l u-small-padding-left u-small-padding-right u-medium-no-padding-left u-medium-no-padding-right">
                    <div className="l-row u-no-margin">
                        <PropositionList
                            key={this.props.selectedProcess}
                            selectedOfferId={this.props.selectedOfferId} {...props}
                            id="propositionLoader"/>
                    </div>
                </div>
            </div>
        );
    }
}

ProductDetailsPropositionsComponent.propTypes = {
    activeFilter: PropTypes.object,
    propositions: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.object,
    ]),
    detailsLabel: PropTypes.string.isRequired,
};


const getCarouselContainerId = () => {
    return "carousel-container";
};

const getCarouselContainer = () => {
    return document.getElementById(getCarouselContainerId())
};
const minHeightBase = 300;
class PropositionList extends Component {

    constructor(props){
        super(props);
        this.state={minHeight:300};
    }
    componentDidMount() {
    }

    getInfoDescription = (key) => {
        const defaultDesc = {
            'discount.text': 'z rabatami'
        };
        return this.props.descriptions[key] ? this.props.descriptions[key] : defaultDesc[key];
    };

    render() {
        const discountDesc = this.getInfoDescription("discount.text");
        const {propositions, ...rest} = this.props;
        return (
            <OraLoader loading={this.props.offersLoading} key={"loader"}
                       id="offerCarouselContainer-loader" parentId="offer-filter-loader"
                       useHeightFixing={false}
                       childrenHeight={this.state.minHeight}
                       className={"u-position-relative"}>


                <div id="offerCarouselContainer" style={{minHeight: this.props.offersLoading?this.state.minHeight:minHeightBase}}
                     className="g-gray1-bg u-position-relative"
                     ref={ref => this.ref = ref}>

            <ul className="opl-switcher-list">
                <li className="opl-switcher-list__item u-small-hide  ">
                    <div className="l-row u-no-margin">
                        {!this.props.addTerminalToOfferBundleNo &&
                        <div className="l-col l-col-1 ">
                        </div>
                        }


                        <div className="l-col l-col-4  u-display_table-cell u-small-block u-vertical-bottom">
                            <p>Abonament</p>
                        </div>
                        <div
                            className={"l-col " +(this.props.addTerminalToOfferBundleNo?" l-col-5":"l-col-4")+  "  u-display_table-cell u-small-block u-vertical-bottom u-text-left"}>
                            <p>{this.props.isB2B ? "Korzyści" : "Internet"}</p>
                        </div>
                        <div
                            className="l-col l-col-small-12 l-col-medium-3 l-col-3  u-display_table-cell u-small-block u-vertical-top u-text-right">
                            <p>Opłata abonamentowa {!this.props.isB2B && this.props.descriptions && (
                                <OraHtmlText>{discountDesc}</OraHtmlText>)}</p>
                        </div>
                    </div>


                </li>
                {!this.props.addTerminalToOfferBundleNo ?
                    propositions && propositions.map((proposition, idx) =>
                        <PropositionRow key={proposition.id + "_" + idx} proposition={proposition} {...rest}/>)
                    :
                    propositions && propositions.filter(proposition => proposition.id === this.props.selectedOfferId).map((proposition, idx) =>
                        <PropositionRow key={proposition.id + "_" + idx} proposition={proposition} {...rest}/>)
                }
            </ul>
                </div></OraLoader>
        )
    }
}

PropositionList.propTypes = {
    activeFilter: PropTypes.object,
    propositions: PropTypes.arrayOf(PropTypes.object),
    detailsLabel: PropTypes.string.isRequired,
};

class PropositionRow extends Component {

    monthlyPaymentsWithBonuses() {
        if (OnlineUtils.getPaymentsForRole(this.props.proposition.payments, this.props.contractRole) && OnlineUtils.getPaymentsForRole(this.props.proposition.payments, this.props.contractRole).totalPayments)
            return OnlineUtils.getPaymentsForRole(this.props.proposition.payments, this.props.contractRole).totalPayments.monthlyPayments
        return {};
    }

    render() {
        const {proposition} = this.props;
        const selectOfferNoPhoneAction = (e) => {
            const li = $(e.target).closest(".li");
            const position = $(li).index();
            this.props.selectOfferNoPhone(proposition.id, position);
        };
        const isSelected = proposition.id === this.props.selectedOfferId && !this.props.addTerminalToOfferBundleNo;

        return (
            <OraSwitcher key={proposition.id + "_switcher"} onChangeHandler={selectOfferNoPhoneAction}
                         isSelected={isSelected} className=" u-margin-l u-no-padding-r  u-border"  switcherClassName="  opl-switcher-list__item u-cursor-pointer u-padding-all-l">
                <div className={"u-large-hide u-medium-hide"}>

                    <PropositionForDeviceMobileView offer={proposition} {...this.props} isSelected={isSelected}
                                                    index={this.props.index}/>
                </div>
                <div className={"u-small-hide"}>

                    <PropositionForDeviceTabletDesktopView offer={proposition} {...this.props} isSelected={isSelected}
                                                    index={this.props.index}/>
                </div>
                {(isSelected) &&
                OnlineUtils.updateOgTag('product:retailer_part_no', proposition.id + '^' + this.props.selectedVariant)
                }
            </OraSwitcher>
        )

    }
}

PropositionRow.propTypes = {
    proposition: PropTypes.object.isRequired,
    detailsLabel: PropTypes.string.isRequired,
};



const StartPrice = (props) => {
    var proposition = props.proposition;
    if (!proposition.deviceData || !proposition.deviceData.inOffer) return null;
    let oneTimePayment = OnlineUtils.formatPrice(proposition.deviceData.inOffer.price.base.oneTimePayment)
    return (
        <div>
            <p className="u-medium-hide u-large-hide u-inline-block u-small-left">Opłata na start</p>
            <p className="u-acc-hide">Opłata na start</p>
            <p className="u-font-bold u-inline-block u-small-right">
                {oneTimePayment} &nbsp;zł
            </p>
        </div>
    )
};

 const mapStateToProps = state => ({
    selectedOfferType: getSelectedOfferType(state),
    selectedOfferId: getSelectedOfferId(state),
    selectedRateplanBaseProductId: getSelectedBaseRatePlanId(state),
    propositionsInContext: getOffersDataInContext(state),
    offersLoading: getOffersLoading(state),
    clientContext: getClientContext(state),
    loyaltyLength: getSelectedLoyaltyLengthValue(state),
    selectedProcess: getSelectedProcessTypeValue(state),
    addTerminalToOfferBundleNo: getAddTerminalToOfferBundleNo(state),
    selectedVariant: getSelectedVariant(state),
    contractRole: getContractRole(state)[0],
    contractRoleInProgress: getContractRoleInProgress(state),
    entryUnderChange: getEntryUnderChange(state),
    softBundleGroup: getSelectedSoftBundleGroup(state),
    isB2B: MarketSegment.isB2B(getMarketContext(state)),
    selectedVases: getSelectedVases(state),

});

const mapDispatchToProps = dispatch => ({
    selectOfferNoPhone: (propositionId, position, deviceId) => dispatch(preSelectOffer(propositionId, position, deviceId)),
    pickDevice: (propositionId, url) => dispatch(pickDevice(propositionId, url))
});

export default connect(mapStateToProps, mapDispatchToProps)(ProductDetailsPropositionsComponent)
